<template>
    <div>
        <basic-header :items="['系统管理','密码修改']"></basic-header>
        <!--选卡-->
        <el-card>
            <el-row type="flex" justify="center" align="middle">
                <el-col :span="8" class="box">
                    <h3 style="text-align: center;margin-bottom: 20px">密码修改</h3>
                    <el-form :model="form" label-width="100px" :rules="rules" ref="form">
                        <el-form-item label="旧密码" prop="password">
                            <el-input type="password" v-model="form.password"></el-input>
                        </el-form-item>
                        <el-form-item label="新密码" prop="newpassword1">
                            <el-input type="password" v-model="form.newpassword1"></el-input>
                        </el-form-item>
                        <el-form-item label="确认新密码" prop="newpassword2">
                            <el-input type="password" v-model="form.newpassword2"></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" @click="submit">确认</el-button>
                        </el-form-item>
                    </el-form>
                </el-col>
            </el-row>
        </el-card>
    </div>
</template>

<script>
    import Header from "../../components/Header";
    export default {

        data() {
            //自定义验证函数
            let v1 = (rule, value, callback) => {
                let reg = /^[0-9a-zA-Z]{6,}$/;
                if (reg.test(value)) {
                    callback();
                } else {
                    callback(new Error("密码必须由六位以上的字母或数字组成"));
                }
            };
            let v2 = (rule, value, callback) => {
                if (value === this.form.newpassword1) {
                    callback();
                } else {
                    callback(new Error("两次输入密码不匹配"));
                }
            };
            return {
                form: {
                    username:sessionStorage.getItem('user'),
                    password: "",
                    newpassword1: "",
                    newpassword2: ""
                },
                rules: {
                    password: {required: true, message: '请输入旧密码', trigger: "blur"},
                    newpassword1: [
                        {required: true, message: '请输入新密码', trigger: "blur"},
                        {validator: v1, trigger: "blur"}
                    ],
                    newpassword2: [
                        {required: true, message: '请确认新密码', trigger: "blur"},
                        {validator: v2, trigger: "blur"}
                    ]
                }
            }
        },
        methods: {
            submit() {
                this.$refs.form.validate(valid => {
                    if (valid) {
                        this.axios.put("/api/admin/changePassword",this.form).then(res=>{
                            if(res.data.code===200){
                                this.$message.success("修改成功");
                                this.$refs.form.resetFields()
                            }else{
                                this.$message.error(res.data.msg);
                            }
                        })
                    } else {
                        this.$message.error('验证失败');
                    }
                })
            }
        },
        //局部注册
        components:{
            "basic-header":Header
        },
        created() {

        },
        mounted() {

        }
    }
</script>

